.backdrop {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  box-shadow: inset 0 0 1px rgba(0,0,0,0.3);
  z-index: 999;
  transition: all .2s ease;
}

.backdrop-open {
  opacity: 1;
  visibility: visible;
  top: 0;
}

.backdrop-close {
  opacity: 0;
  visibility: hidden;
  transition-delay: .3s;
}